import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './User.css';
import $ from 'jquery';
import CollapsibleText from '../../../components/CollapsibleText';
const propTypes = {
    userDetail:PropTypes.object.isRequired,
    onReNew:PropTypes.func.isRequired
};
class User extends Component {
    state={
        index:0,
        userdata:'',
        userdata2:'',
        userDetail:this.props.userDetail
    }
    changeIndex=(num)=>{
        const index=num;
        this.setState({
            index
        });
    }
    dealUserIcon=()=>{
        var uIcon=this.refs.uIcon;    
        var userDetail=Object.assign({},this.state.userDetail,{icon:uIcon.value});
        this.setState({
            userDetail
        });
        $.ajax({
                url:"/readshare/user/person-info/update",
                data:{id:this.state.userDetail.userId,operate:"changeIcon",icon:uIcon.value},
                method:'GET',
                success:this.renew
        });            
    }
    renew=(data)=>{
        // alert('我要去更新父亲的参数');
        this.props.onReNew();
    }
    dealUserPdw=()=>{
        var userdata2='';
        var flag=true;
        var upwd1=this.refs.upwd1;    
        var upwd2=this.refs.upwd2; 
        if(upwd1.value!==upwd2.value){
            userdata2='两次输入的密码不相等';
            flag=false;
        }else if(upwd1.value===''){
            userdata2='密码不能为空！';
            flag=false;
        }else if(upwd1.value.length<6){
            userdata2='密码不能小于6位数';
            flag=false;   
        }   
        if(flag){
            userdata2='修改成功！';
            var userDetail=Object.assign({},this.state.userDetail,{password:upwd1.value});
            this.setState({
                userDetail
            });
            $.ajax({
                url:"/readshare/user/person-info/update",
                data:{id:this.state.userDetail.userId,operate:"changePassword",password:upwd1.value},
                method:'GET',
                success:this.renew
            });  
            this.setState({
                userdata2
            });
        }
        this.setState({
            userdata2,
        });
           
    }
    dealUserName=()=>{
        var userdata='';
        var flag=true;
        var uname=this.refs.uname;
        if(uname.value===''){
            userdata='用户名不能为空！';
            flag=false;
        }else if(uname.value.length>6){
            userdata='用户名不能超过6个字';
            flag=false;            
        }
        if(flag){
            userdata='修改成功！';
            var userDetail=Object.assign({},this.state.userDetail,{userName:uname.value});
            this.setState({
                userDetail
            });       
            $.ajax({
                url:"/readshare/user/person-info/update",//要放在source里面的哦
                data:{id:this.state.userDetail.userId,operate:'changeName',name:uname.value},
                method:'GET',
                success:this.renew
            });      
            this.setState({
                userdata,
            });             
        }
        this.setState({
            userdata,
        });
    }
    bookshelfDelete=(index)=>{
       
        var bookshelf=this.state.userDetail.bookshelf;
        var bookID=this.state.userDetail.bookshelf[index].bookId;
        bookshelf.splice(index,1);
        var userDetail=Object.assign({},this.state.userDetail,{bookshelf:bookshelf});
        this.setState({
            userDetail
        });
        $.ajax({
                url:"/readshare/user/operation/delete",//要放在source里面的哦
                data:{id:this.state.userDetail.userId,operate:'deleteBookself',bookID:bookID},
                method:'GET',
                success:this.renew
        });   
    }
    loveBooks=(index)=>{
        var loveBooks=this.state.userDetail.loveBooks;
        var bookID=this.state.userDetail.loveBooks[index].bookId;
        loveBooks.splice(index,1);
        var userDetail=Object.assign({},this.state.userDetail,{loveBooks:loveBooks});
        this.setState({
            userDetail
        });
            $.ajax({
                url:"/readshare/user/operation/delete",//要放在source里面的哦
                data:{id:this.state.userDetail.id,operate:'deleteLoveBooks',bookID:bookID},
                method:'GET',
                success:this.renew
            });   
    }
    commentDelete=(index)=>{
        var comment=this.state.userDetail.comment;
        var bookID=this.state.userDetail.Comment[index].bookId;
        comment.splice(index,1);
        var userDetail=Object.assign({},this.state.userDetail,{comment:comment});
        this.setState({
            userDetail
        });

            $.ajax({
                url:"/readshare/user/operation/delete",//要放在source里面的哦
                data:{id:this.state.userDetail.id,operate:'deleteComment',bookID:bookID},
                method:'GET',
                success:this.renew
            });   
    }
    render() {    
        return (
            <div className="user">
                <div className="m-mainctrl">
                    <div className="widget-content">
                        <ul>
                            <li className="title">
                                <img src="/readshare/source/other/1.png" alt=""/>
                                 个人信息
                            </li>
                            <li>个人资料</li>
                            <li>账号绑定</li>
                        </ul>
                    </div>
                    <div className="widget-content">
                        <ul>
                                <li className="title">
                                    <img src="/readshare/source/other/3.PNG" alt=""/>                                
                                    我的云阅读
                                </li>
                                <li onClick={()=>{this.changeIndex(3);}} className={this.state.index===3?'active':''}>我的书架</li>
                                <li onClick={()=>{this.changeIndex(4);}} className={this.state.index===4?'active':''}>我的收藏</li>
                                <li onClick={()=>{this.changeIndex(5);}} className={this.state.index===5?'active':''}>我的评论</li>
                                <li onClick={()=>{this.changeIndex(6);}} className={this.state.index===6?'active':''}>我已阅读</li>
                        </ul>
                    </div>
                    <div className="widget-content">
                        <ul>
                            <li className="title">
                               <img src="/readshare/source/other/2.PNG" alt=""/>                                                            
                                账户管理
                            </li>
                            <li onClick={()=>{this.changeIndex(7);}} className={this.state.index===7?'active':''}>修改用户名</li>
                            <li onClick={()=>{this.changeIndex(8);}} className={this.state.index===8?'active':''}>修改密码</li>
                            <li onClick={()=>{this.changeIndex(9);}} className={this.state.index===9?'active':''}>修改头像</li>
                        </ul>
                    </div>                    
                </div>
                <div className="m-mainbox">
                    <div className="m-newpro">
                        <div className="head">
                                <div className="box-1">
                                    <img src={this.state.userDetail.icon!==null?'userDetail.icon':'/readshare/source/other/deIcon.png'} alt=""/>
                                </div>  
                                <div className="box-2">
                                    <h4 className="b-info">
                                        <span>{this.state.userDetail.userName}</span>
                                        <span>{this.state.userDetail.sex}</span>
                                    </h4>
                                    {/* <div className="sdbox">
                                            <span></span>
                                            <span></span>
                                    </div> */}
                                </div>      
                        </div>
                         <div className="body">
                            {
                                (this.state.index===3)&&(this.state.userDetail.bookshelf!==null)&&(
                                    this.state.userDetail.bookshelf.map((item,index)=>(
                                        <div className="bookWrapper" key={item.coverUrl}>
                                            <div className="delete" onClick={()=>{this.bookshelfDelete(index);}}><img src="/readshare/source/other/d.png" alt=""/></div>
                                            <div className="bookInner">
                                                <img src={item.coverUrl} alt=""/>
                                                <div className="bookname">{item.bookName}</div>
                                            </div>
                                        </div>
                                    ))
                                )
                            }
                            {
                                (this.state.index===4)&&(this.state.userDetail.loveBooks!==null)&&(
                                    this.state.userDetail.loveBooks.map((item,index)=>(
                                        <div className="bookWrapper" key={item.coverUrl}>
                                            <div className="delete"  onClick={()=>{this.loveBooks(index);}}><img src="/source/other/d.png" alt=""/></div>
                                            <div className="bookInner">
                                                <img src={item.coverUrl} alt=""/>
                                                <div className="bookname">{item.bookName}</div>
                                            </div>
                                        </div>
                                    ))
                                )
                            }
                            {
                                (this.state.index===5)&&(this.state.userDetail.comment!==null)&&(
                                    this.state.userDetail.comment.map((item,index)=>(
                                        <div className="bookWrapper-o" key={item.coverUrl}>
                                                <div className="bookInner-o">
                                                    <img src={item.coverUrl} alt=""/>
                                                    <div className="bookname">{item.bookName}</div>
                                                </div>
                                                <div className="mark-o">
                                                    我的评论：<CollapsibleText>{item.mark}</CollapsibleText>
                                                </div>
                                                <div className="delete"  onClick={()=>{this.commentDelete(index);}}><img src="/source/other/d.png" alt=""/></div>
                                        </div>
                                    ))
                                )
                            }
                            {
                                (this.state.index===6)&&(this.state.userDetail.hadRead!==null)&&(
                                    this.state.userDetail.hadRead.map(item=>(
                                        <div className="bookWrapper" key={item.coverUrl}>
                                            <div className="bookInner">
                                                <img src={item.coverUrl} alt=""/>
                                                <div className="bookname">{item.bookName}</div>
                                            </div>
                                        </div>
                                    ))
                                )
                            }

                            {
                                (this.state.index===7)&&(
                                    <div className="s-wrapper">
                                        <div className="default">
                                            <div className="tip">请输入新的用户名:</div>
                                            <input type="text" id="uname" name="uname" ref="uname"/>
                                            <span  className="show" id="showuser" ref="showuser">{this.state.userdata}</span>
                                            <button type="submit" onClick={this.dealUserName}>提交</button>
                                        </div>
                                    </div>
                                )
                            }
                             {
                                (this.state.index===8)&&(
                                    <div className="s-wrapper">
                                        <div className="default">
                                            <div className="tip">请输入新的密码:</div>
                                            <input type="password" id="upwd1" name="upwd1" ref="upwd1"/>
                                            <div className="tip">请再次确认密码:</div>
                                            <input type="password" id="upwd2" name="upwd2" ref="upwd2"/>
                                            <span className="show" id="showpwd" ref="showpwd">{this.state.userdata2}</span>
                                            <button type="submit" onClick={this.dealUserPdw}>提交</button>
                                        </div>
                                    </div>
                                )
                            }
                             {
                                (this.state.index===9)&&(
                                    <div className="s-wrapper">
                                        <div className="default">
                                            <div className="tip">请传入正确的图片链接:</div>
                                            <input type="text" id="uIcon" name="uIcon" ref="uIcon"/>
                                            <button type="submit" onClick={this.dealUserIcon}>提交</button>
                                        </div>
                                    </div>
                                )
                            }
                         </div>
                    </div>
                </div>
            </div>
        );
    }
}


User.propTypes = propTypes;


export default User;




