import React, { Component } from 'react';
import './index.css';
import { browserHistory } from 'react-router';
import $ from 'jquery';
import {Link} from 'react-router-dom';
class Login extends Component{
    state={
        userdata:'',
        Psdata:'',
    }
    deal=(event)=>{
        event.preventDefault();
        var userdata='',Psdata='';
        var flag=true;
        var uname=this.refs.uname;
        var upwd=this.refs.upwd;
        if(uname.value===''){
            userdata='用户名不能为空！';
            flag=false;
        }else if(uname.value.length!==11){
            userdata='请输入11位手机号码！';
            flag=false;            
        }
        if(upwd.value===''){
            Psdata='密码不能为空！';
            flag=false;
        }else if(upwd.value.length<6){
            Psdata='密码不能小于六位数！';
            flag=false;
        }
        this.setState({
            userdata,
            Psdata
        });
        if(flag){
            $.ajax({
                url:"/readshare/user/login",//要放在source里面的哦
                data:{userName:uname.value,userPassword:upwd.value},
                method:'GET',
                success:this.addData
            });
        }
    }
    addData=(data)=>{
        var upwd=this.refs.upwd;        
        var loginSuccess=data.loginSuccess;
        var userId=data.id;
        var path =`/readshare/home?${userId}`;
        if(loginSuccess){
            browserHistory.push(path);
            window.history.go(0);
        }else{
            var  Psdata="密码错误，请重新输入！";
            upwd.value='';
            this.setState({
                Psdata
            });
        }
    }
    render(){
    const {userdata,Psdata}=this.state;
    return (
      <div className="login">
      <div className="main">
         <div className="container">
             <div className="bgPic">
             </div>
             <div className="register">
                 <div className="title">
                     <span>登录</span>
                     <Link to="/readshare/register" className="link">去注册</Link>
                 </div>
                 <form className="form" onSubmit={this.deal}>
                     <div className="default">
                        <input type="text" id="uname" name="uname" placeholder="手机号" ref="uname"/>
                         <span id="showuser" ref="showuser">{userdata}</span>
                     </div>
                     <div className="default">
                         <input type="password" id="upwd" name="upwd" placeholder="密码" ref="upwd"/>
                         <span className="" id="showPs" ref="showPs">{Psdata}</span>
                     </div>
                     <div className="submit">
                         <span className="notice">
                             <a href="#的">忘记密码</a>
                         </span>
                         <button className="sub" type="submit">登录
                         </button>                            
                     </div>
                  </form>
             </div>
         </div>
     </div>
     </div>
  );
 }
};

export default Login;


