import React, { Component } from "react";
import "./App.css";
import { BrowserRouter, Route,Switch } from 'react-router-dom';
import Login from './routes/login/index';
import Register from './routes/register/index';
import Home from './routes/home/index';
import Detail from './routes/detail/index';

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <Switch>
          <Route   path="/readshare/" exact component={Login} />
          <Route  path="/readshare/register" exact component={Register} />
          <Route  path="/readshare/home" exact component={Home} />
          <Route  path="/readshare/detail" exact component={Detail} />
        </Switch>
      </BrowserRouter>
    );
  }
}

export default App;



