import React from 'react';
// import PropTypes from 'prop-types';

import Slider from 'react-slick';
import './Slider.css';

const PosterSlide = () => {
  const settings = {
    dots: true,
    autoplay: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    className: 'posterSlide',
    dotsClass: 'posterSlide__dots'
  };

  return (
    <Slider {...settings} >
            <div>
            <img className="posterSlide__image" src="/readshare/source/slide/slider1.jpeg" alt=""/>
            </div>
            <div>
            <img className="posterSlide__image" src="/readshare/source/slide/slider2.jpeg" alt=""/>
            </div>
            <div>
            <img className="posterSlide__image" src="/readshare/source/slide/slider3.jpeg" alt=""/>
            </div>
            <div>
            <img className="posterSlide__image" src="/readshare/source/slide/slider4.jpeg" alt=""/>
            </div>
    </Slider>
  );
};


export default PosterSlide;


