import React, { Component } from 'react';
import $ from 'jquery';
import './index.css';//因为同级编译成css
import PropTypes from 'prop-types';
class Detail extends Component {
    state={
        bookDeatil:{},
        ifShowComment:false,
        ifShowReplyComment:false,
        commentList:[],
        boolBookshelf:false,
        boolLove:false,
        boolRead:false
    }
    componentWillMount() {
        this.getData();
        this.getCommentData();
    }
    showComment=()=>{
        var ifShowComment=true;
        this.setState({
            ifShowComment
        });
    }
    showBeReply=(index)=>{
        var ifShowReplyComment;
        if(this.state.ifShowReplyComment===index){
            ifShowReplyComment=false;
        }else{
            ifShowReplyComment=index;
        }
        this.setState({
            ifShowReplyComment
        });
    }
    submitComment=(event)=>{
        event.preventDefault();   
        var textDom= this.refs.textarea;
        var comment=  textDom.value;
        // var d=new Date();
        // var year=d.getFullYear();
        // var month=d.getMonth()+1;
        // var date=d.getDate();
        // var hour=d.getHours()<10?"0"+d.getHours():d.getHours();
        // var minute=d.getMinutes()<10?"0"+d.getMinutes():d.getMinutes();
        // var second=d.getSeconds()<10?"0"+d.getSeconds():d.getSeconds();
        // var currentTime=year+'-'+month+'-'+date+' '+hour+':'+minute+':'+second;
        if(comment===''){
            alert('评论不能为空！');
        }else{
            alert('提交成功!');
            this.setState({
                ifShowComment:false
            });
        }
        var userID=window.location.search.slice(1).split('&&')[1];
        $.ajax({
            url:"/readshare/user/operation/add",//要放在source里面的哦
            data:{
                 userID:userID,
                 operate:"addComment",
                 bookName:this.state.bookDeatil.title,                                                                    
                 bookId:window.location.search.slice(1).split('&&')[0],                                                
                 photoUrl:this.state.bookDeatil.image,
                 mark:comment
             },
            success:this.getCommentData
        });
    }
    getCommentData=()=>{//获取这本书的所有评论
        $.ajax({
            type: "GET",
            url: "/readshare/book/comments",
            data:{bookId:window.location.search.slice(1).split('&&')[0],userId:window.location.search.slice(1).split('&&')[1]},
            success: this.addCommentData
          });
    }
    addCommentData=(data)=>{
            var boolBookshelf=data.boolBookshelf;
            var boolLove=data.boolLove;
            var boolRead=data.boolRead;
            var commentList=data.comments;
            this.setState({
                commentList,
                boolBookshelf,
                boolLove,
                boolRead
            });
            console.log(this.state.commentList);
    }
    getData = () => {
        $.ajax({
            type: "GET",
            url: "https://api.douban.com/v2/book/" + window.location.search.slice(1).split('&&')[0],
            dataType: "jsonp",
            success: this.addBookData
          });
      };
    addBookData=(data)=>{
        const bookDeatil=data;     
        this.setState({
            bookDeatil
        });
      }
      bookshelfChange=()=>{
        var boolBookshelf = !this.state.boolBookshelf;
        this.setState({
            boolBookshelf
        });
        if(boolBookshelf){//增加
            $.ajax({
                url:"/readshare/user/operation/add",//要放在source里面的哦
                data:{userId: window.location.search.slice(1).split('&&')[1],operate:'bookshelfChange',
                bookId:window.location.search.slice(1).split('&&')[0],          
                bookName:this.state.bookDeatil.title,                                                   
                photoUrl:this.state.bookDeatil.image,
               },
                method:'GET',
                success:this.getCommentData
            });  
        }else{
            $.ajax({
                url:"/readshare/user/operation/delete",//要放在source里面的哦
                data:{id:window.location.search.slice(1).split('&&')[1],operate:'deleteBookself',bookID:window.location.search.slice(1).split('&&')[0]},
                method:'GET',
                success:this.getCommentData
            });  
        }
           
    }
    loveBooksChange=()=>{
        var boolLove = !this.state.boolLove;
        this.setState({
            boolLove
        });
        if(boolLove){
            $.ajax({
                url:"/readshare/user/operation/add",//要放在source里面的哦
                data:{userId: window.location.search.slice(1).split('&&')[1],
                    operate:'loveBooksChange',
                    bookId:window.location.search.slice(1).split('&&')[0],                                            
                    photoUrl:this.state.bookDeatil.image,
                    bookName:this.state.bookDeatil.title                                                               
                },
                method:'GET',
                success:this.getCommentData
            }); 
        }else{
            $.ajax({
                url:"/readshare/user/operation/delete",//要放在source里面的哦
                data:{  id:window.location.search.slice(1).split('&&')[1],
                        operate:'deleteLoveBooks',
                        bookID:window.location.search.slice(1).split('&&')[0],
                        photoUrl:this.state.bookDeatil.image,
                        bookName:this.state.bookDeatil.title           
                      },
                method:'GET',
                success:this.getCommentData
            });  
        }      
    }
    readChange=()=>{
        var boolRead = !this.state.boolRead;
        this.setState({
            boolRead
        });
        if(boolRead){
            $.ajax({
                url:"/readshare/user/operation/add",//要放在source里面的哦
                data:{userId: window.location.search.slice(1).split('&&')[1],operate:'readChange',
                bookId:window.location.search.slice(1).split('&&')[0],
                bookName:this.state.bookDeatil.title           
               },
                method:'GET',
                success:this.getCommentData
            });   
        }
    }
    replayfun=(comId,event)=>{
        alert(event);
        alert(comId);
        event.preventDefault();   
        var textDom= this.refs.textarea2;
        var comment=  textDom.value;
        if(comment===''){
            alert('评论不能为空！');
        }else{
            alert('提交成功!');
        }
        var userID=window.location.search.slice(1).split('&&')[1];        
        $.ajax({
            url:"/readshare/book/comments/reply",//要放在source里面的哦
            data:{
                 userId:userID,
                 comId:comId,
                 mark:comment
             },
            method:'GET',
            success:this.getCommentData
        });
    }
    beReply=(replyId,event)=>{
        alert(event);
        alert(replyId);
        event.preventDefault();   
        var textDom= this.refs.textarea3;
        var comment=  textDom.value;
        if(comment===''){
            alert('回复不能为空！');
        }else{
            alert('提交成功!');
        }     
        $.ajax({
            url:"/readshare/book/comments/reply/update",//要放在source里面的哦
            data:{
                
                 replyId:replyId,
                 beReply:replyId,
             },
            method:'GET',
            success:this.comment
        });
    }
  render() {
      const bookDeatil=this.state.bookDeatil;
      const commentList=this.state.commentList;
      const boolBookshelf=this.state.boolBookshelf;
      const boolLove=this.state.boolLove;
      const boolRead=this.state.boolRead;
      const ifShowReplyComment=this.state.ifShowReplyComment;
    return (
        <div className="detail">
            <div className="wrapper">
                {
                     <div className="info">
                            <div className="title">
                                <h2>{bookDeatil.title}</h2>
                            </div>
                            <div className="detail">
                                    <div className="detail-left">
                                        <img src={bookDeatil.image} alt=""/>
                                        <div>
                                            <ul>
                                                <li><span>作者:</span>{bookDeatil.author?bookDeatil.author:'未知'}</li>
                                                <li><span>出版社：</span>{bookDeatil.publisher?bookDeatil.publisher:'未知'}</li>
                                                <li><span>出版年：</span>{bookDeatil.pubdate?bookDeatil.pubdate:'未知'}</li>
                                                <li><span>页数：</span>{bookDeatil.pages?bookDeatil.pages:'未知'}</li>
                                                <li><span>定价：</span>{bookDeatil.price?bookDeatil.price:'未知'}</li>                                    
                                            </ul>
                                        </div>
                                    </div>
                                    <div className="detail-right">
                                            <div>
                                               <img src={(!boolBookshelf)?"/readshare/source/other/bookshelf.png":"/readshare/source/other/bookshelf1.png"} alt="" onClick={()=>{this.bookshelfChange();}}/><span>{!boolBookshelf?"加入书架":"取消加入"}</span>
                                            </div>
                                            <div>
                                               <img src={(!boolLove)?"/readshare/source/other/love.png":"/readshare/source/other/love1.png"} alt="" onClick={()=>{this.loveBooksChange();}}/><span>{!boolLove?"加入收藏":"取消收藏"}</span>                                                
                                            </div>
                                            <div>
                                               <img src={(!boolRead)?"/readshare/source/other/read.png":"/readshare/source/other/read1.png"}  alt="" onClick={()=>{this.readChange();}}/><span>{!boolRead?"已经阅读":"没有阅读"}</span>                                         
                                            </div>
                                    </div>
                                </div>
                                <div className="related">
                                    <h4>内容简介</h4>
                                    <p>{bookDeatil.summary}</p>
                                </div>
                                <div className="operate">
                                    <div className="icon" onClick={this.showComment}><img src="/readshare/source/other/pancle.png" alt=""/></div>
                                    <span className="icon2">写书评</span>
                                </div>
                                {
                                    this.state.ifShowComment&&(
                                    <div className="myComment">
                                        <form action="">
                                            评论：<br/>
                                            <textarea ref="textarea" name="Comment" id="Comment" cols="100" rows="10" placeholder="随便说点什么吧...."></textarea>
                                            <input type="submit" value="提交" onClick={this.submitComment}/>
                                        </form>
                                    </div>)
                                }   
                                <div className="commentList">
                                    {(commentList!==null)&&(
                                      commentList.map(item=>(
                                         <div className="com" key={item.comId}>
                                            <div className="top">
                                                <div className="icon"><img src={item.icon} alt=""/></div>
                                                <span className="uname">{item.username}</span>
                                                <div className="time">{item.createTime}</div>
                                                <div className="mark">
                                                    {item.mark}
                                                </div>
                                            </div>
                                            {/* {
                                                (item.reply!==null)&&(
                                                    <div className="bott">
                                                    {item.reply.map(item2=>(
                                                   <div className="Reply" key={item2.replyId}>
                                                          <div className="ReplyDetail">
                                                              <span>{item2.username}: </span>
                                                              <span>{item2.mark}</span>
                                                              <span onClick={()=>{this.showBeReply(item2.replyId);}}>{((item.userid.toString())===window.location.search.slice(1).split('&&')[1])&&((item2.beReply===null))&&(<img src="/source/other/re.png" alt=""/>)}</span>
                                                              {(ifShowReplyComment===item2.replyId)&&(
                                                                  <form action="" className="inner">
                                                                    <textarea ref="textarea3" name="Comment" id="Comment" cols="90" rows="1" placeholder={`${item.username}回复${item2.username}`}></textarea>
                                                                    <input type="submit" value="提交" onClick={this.replayfun.bind(this,item2.replyId)}/>
                                                                  </form>
                                                              )}
                                                          </div>
                                                          {
                                                              (item2.beReply!==null)&&(<div className="beReply"><div> {item.username } <span> 回复 </span>{ item2.username}：{item2.beReply}</div></div>)
                                                          }                                           
                                                   </div> ))}
                                              </div>
                                                )
                                            } */}
                                                 {/* <form action="" className="outer">
                                                    <textarea ref="textarea2" name="Comment" id="Comment" cols="90" rows="1" placeholder="评论"></textarea>
                                                    <input type="submit" value="提交" onClick={this.replayfun.bind(this,item.comId)}/>
                                                </form> */}
                                         </div>
                                      ))
                                    )
                                    }
                                </div>  
                      </div>

                }
                </div>
           
        </div>
    );
  }
}
 export default Detail;




