import React, { Component } from 'react';
import Slider from './components/Slider';
import Lemenu from './components/Lemenu';
import User from './components/User';
import './index.css';//因为同级编译成css
import $ from 'jquery';
import {Link} from 'react-router-dom';
// import { Link } from 'react-router-dom';
class Home extends Component {
    state={
        books:[],
        userDetail:{},
        page:true
    }
      onInputF = () => {
        
        var input=this.refs.searchInput;
        var value=input.value;
        
        
        $.ajax({
                type:'GET',
                url:"https://api.douban.com/v2/book/search",
                dataType:'jsonp',
                data:'q='+value+'&count=6',
                success:this.addData
            });
      };
      addData=(data)=>{
        const {books}=data;          
        this.setState({
            books
        });
      }
      componentWillMount() {
            this.getData();
      }
      getData=()=>{
        $.ajax({
            url:"/readshare/user/getusermsg",//要放在source里面的哦
            data:{userId:window.location.search.slice(1)},
            method:'GET',
            success:this.addUserData
        });
      }
      addUserData=(data)=>{
        var userDetail=data.user;
        this.setState({
            userDetail
          });
      }
        changePage=()=>{
            this.setState({
                page:false
            });
        }
        onReNew=()=>{
            this.getData();
        }
    render() {
        const {books,userDetail,page}=this.state;
        return (
            <div className="home">
            <div className="userDeatil">
                <img src={userDetail.icon!==null?`${userDetail.icon}`:'/readshare/source/other/deIcon.png'} alt=""/>
                <div className="username">
                    <span onClick={this.changePage}>{userDetail.userName}</span>,欢迎你！
                </div>
            </div>
                <div className="nav-book">
                    <div className="nav-wrap">
                        <div className="nav-primary">
                            <div className="nav-search">
                                <form action="">
                                    <input ref="searchInput" type="text" className="input" placeholder="书名" onInput={this.onInputF}/>
                                    <input type="submit" value="" className="submit"/>
                                </form>
                                <div id="search_suggest">
                                  <ul className="search_list">
                                        {
                                            books.map(item=>
                                                <li key={item.id}>
                                                     <Link to={`/readshare/detail?${item.id}&&${userDetail.userId}`} className="link">
                                                        <img src="/readshare/source/other/fe.png" width="40" alt=""/>
                                                        <div>
                                                            <strong>{item.title}</strong>
                                                            <p>{item.author[0]}</p>
                                                        </div>
                                                     </Link>
                                                </li>
                                            )
                                        }
                                  </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="h-body">
                    {
                        page?(<div className="wrapper">
                        <div className="slider-fa">
                            <div className="home__slide">
                                        <div className="home__slideWrap">
                                            <Slider></Slider>
                                        </div>
                            </div>    
                        </div>
                        <div className="menu">
                                <Lemenu></Lemenu>            
                        </div>           
                        </div>):(
                            <div className="wrapper">
                                <User userDetail={userDetail} onReNew={this.onReNew}></User>
                            </div>
                        )
                    }        
                </div>
            
            </div>
        );
    }
}
export default Home;


