
import "./index.css";
import React, { Component } from 'react';
import { Link } from "react-router-dom";
import $ from 'jquery';
class Register extends Component{
  state={
    phoneData:'',
    nameData:'',
    passwordData:'',
    tipSucc:''
  }
  deal=(event)=>{
    event.preventDefault();
    var flag=true;    
    var phone=this.refs.uphone;
    var phoneData='';
    if(phone.value===''){
      phoneData='用户名不能为空！';
      flag=false;
    }else if(phone.value.length!==11){
      phoneData='请输入11位手机号码！';
      flag=false;            
    }
    var uname=this.refs.uname;
    var nameData='';
    if(uname.value===''){
      nameData='用户名不能为空！';
      flag=false;
    }else if(uname.value.length>6){
      nameData='用户名不能超过6个字！';
        flag=false;            
    }
    var upwd1=this.refs.upwd1;    
    var upwd2=this.refs.upwd2; 
    var passwordData='';
    if(upwd1.value!==upwd2.value){
      passwordData='两次输入的密码不相等！';
        flag=false;
    }else if(upwd1.value===''){
      passwordData='密码不能为空！';
        flag=false;
    }else if(upwd1.value.length<6){
      passwordData='密码不能小于6位数！';
        flag=false;   
    } 
    if(flag){
      $.ajax({
        type: 'GET',
        // url:'/source/data/logindata.json',
        url:'/readshare/user/register',
        data: {userPhone:phone.value,userName:uname.value,userPassword:upwd1.value},
        success:this.addData
      });
      // 清空所有内容，并显示注册成功
    }
    this.setState({
      phoneData,
      nameData,
      passwordData,
    });
  };
  addData=(data)=>{
    var phone=this.refs.uphone;
    var uname=this.refs.uname;
    var tipSucc='';
    var upwd1=this.refs.upwd1;    
    var upwd2=this.refs.upwd2; 
      phone.value='';
      uname.value='';
      upwd1.value='';
      upwd2.value='';
      if(data.success){
        tipSucc='恭喜你，注册成功!';
        this.setState({
          tipSucc
        });
      }else{
        tipSucc='该手机号已经被注册!';
        this.setState({
          tipSucc
        });
      }
    }
  render (){
    var {phoneData,nameData,passwordData,tipSucc}=this.state;
    return (
      <div className="register">
      <div className="main">
        <div className="container">
          <div className="bgPic" />
          <div className="register">
            <div className="title">
              <span>注册</span>
              <Link to="/readshare/">去登录</Link>
            </div>
            <form action="" onSubmit={this.deal}>
              <span className="tipSucc">{tipSucc}</span>
              <div className="default">
                <input
                  type="text"
                  id="uphone"
                  name="uphone"
                  placeholder="手机号"
                  ref="uphone"
                />
                <span id="showPhone" >{phoneData}</span>
              </div>
              <div className="default">
                <input
                  type="text"
                  id="uname"
                  name="uname"
                  placeholder="用户名"
                  ref="uname"
                />
                <span id="showName" >{nameData}</span>                
              </div>
              <div className="default">
                <input
                  type="password"
                  id="upwd1"
                  name="upwd1"
                  placeholder="密码"
                  ref="upwd1"                  
                />
              </div>
              <div className="default">
                <input
                  type="password"
                  id="upwd2"
                  name="upwd2"
                  placeholder="确认密码"
                  ref="upwd2"
                />
                <span id="showPassword" >{passwordData}</span>                                
              </div>
              <div className="submit">
                <span className="notice">
                  点击"注册"代表您同意遵守 用户协议 和 隐私条款
                </span>
                <button className="sub">注册</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
    );
  }

}

export default Register;



